<template>
  <div class="search_container">
    <!-- 头部部分 -->
    <div class="search_top_container">
      <img
        @click="backClick"
        src="data:image/png;base64,"
        alt
      />
      <div class="search_input">
        <input type="text" v-model="ipt" placeholder="请输入内容" />
      </div>
      <div
        class="cancel_container"
        @click="searchClick"
        :style="this.ipt.length>0?'color:#333333':''"
        v-if="this.ipt"
      >搜索</div>
      <div class="cancel_container" v-if="!this.ipt" @click="cancelClick">取消</div>
    </div>
    <!-- 历史搜索部分 -->
    <div class="search_history_contanier" v-show="!this.ipt">
      <div>
        <h3 style="margin-left:20px;">历史搜索</h3>
      </div>
      <div @click="delClick">
        <van-icon style="margin-right:20px;" name="delete" />
      </div>
    </div>
    <!-- 搜索历史 列表-->
    <div class="history_List_container" v-show="!this.ipt">
      <div
        class="history_list_item"
        @click="historyClick(item)"
        v-for="(item,index) in historyList"
        :key="index"
      >{{item.name}}</div>
    </div>
    <!-- 搜索列表部分 -->
    <div class="search_list_container" v-show="showList">
      <div class="list_item"  v-for="(item,index) in searchList" :key="index">
        <div class="item_top">
          <div style="color:#333333;font-size:14px;">{{item.title}}</div>
          <div>
            <span></span>~
            <span></span>|
            <span>共{{item.total_periods}}课时</span>
          </div>
        </div>
        <div class="item_avatar" v-for="(v,index) in item.teachers_list" :key="index">
          <img style="margin-left:10px;box-sizing:border-box;" :src="v.teacher_avatar" />
          <div
            style="margin-left:10px;box-sizing:border-box;font-size:10px;color:rgba(0,0,0,0.45);"
          >{{v.teacher_name}}</div>
        </div>
        <div class="item_bottom">
          <div style="margin-left:10px;color:#595959;font-size:12px;">{{item.sales_num}}人已报名</div>
          <div
            style="margin-right:10px;"
            :style="item.price==0?'color:green':'orange'"
          >{{item.price|price}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      ipt: "",
      searchList: [],
      show: false,
      historyList: JSON.parse(localStorage.getItem("iptList")) || []
    };
  },
  computed: {
    showList: function() {
      let showing = true;
      if (this.ipt.length == 0) {
        showing = false;
      }
      return showing;
    }
  },
  watch: {
    show: {
      handler: function(newVal, oldVal) {
        console.log(newVal);
        console.log(oldVal);
      }
    }
  },
  filters: {
    price(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    }
  },
  methods: {
    // 搜索历史输入input值
    historyClick(item){
      this.ipt=item.name;
    },
    // 删除按钮
    delClick() {
      this.historyList = [];
      localStorage.setItem("iptList", JSON.stringify(this.historyList));
    },
    backClick() {
      window.history.back();
    },
    cancelClick() {
      this.$router.push("/course");
    },

    // 搜索按钮事件
    searchClick() {
      this.$http
        .get(
          `/api/app/courseBasis?limit=10&page=1&course_type=0&keywords=${this.ipt}`
        )
        .then(resp => {
          console.log(resp.data);
          this.searchList = resp.data.data.list;
        });

      // 存储
      let history = this.historyList;
      let obj = {
        name: this.ipt
      };
      history.unshift(obj);
      localStorage.setItem("iptList", JSON.stringify(history));
    }
  },
  mounted() {}
};
</script>
<style  scoped>
.search_top_container {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #999;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
img {
  width: 20px;
  height: 20px;
  margin-left: 20px;
}
.search_input {
  width: 80%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
input {
  width: 90%;
  height: 90%;
  border: none;
  border-radius: 30px;
  background: #3333;
  padding-left: 20px;
  box-sizing: border-box;
}
.cancel_container {
  width: 11%;
  height: 100%;
  color: #595959;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search_history_contanier {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: Center;
  margin-top: 20px;
  position: fixed;
  top: 50px;
}
.search_list_container {
  width: 100%;
  position: fixed;
  top: 60px;
}
.search_list_item {
  width: 100%;
  height: 180px;
  border: 1px solid red;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.list_item {
  width: 99%;
  height: 200px;
  display: flex;
  flex-direction: Column;
  justify-content: space-around;
  align-items: center;
  margin: 10px 0px;
  background: #fff;
}
.item_top {
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.item_avatar {
  width: 100%;
  height: 45px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.item_avatar img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.item_bottom {
  width: 100%;
  height: 35px;
  display: flex;
  justify-content: space-between;
  align-items: Center;
}
.history_List_container {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-top: 50px;
}
.history_list_item {
  margin-left: 10px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #e2dddd;
  color: #333333;
}
</style>