<template>
  <div class="course_container">
    <!-- 头部 -->
    <div class="course_header_container">
      <div class="course_header_right">
        <div>特色课</div>
        <div @click="searchClick">
          <img style="width:30px;height:30px;" src="../assets/sousuo.png" alt />
        </div>
      </div>
    </div>
    <!-- 筛选部分 -->
    <div class="course_select_container">
      <van-dropdown-menu active-color="orange">
        <van-dropdown-item title="分类" ref="ok">
          <div class="course_classify_container">
            <div class="course_top_wrap">
              <p>{{this.classifyData[0].name}}</p>
              <div style="width:100%;display:Flex;justify-content:flex-start;flex-wrap:wrap">
                <div
                  :class="selIndex==index?'course_top_item_active':'course_top_item'"
                  v-for="(item,index) in this.classifyData[0].child"
                  :key="index"
                  @click="classifyClick(index)"
                >{{item.name}}</div>
              </div>
            </div>
            <div class="course_top_wrap">
              <p>{{this.classifyData[1].name}}</p>
              <div style="width:100%;display:Flex;justify-content:flex-start;flex-wrap:wrap">
                <div
                  :class="gradeIndex==index?'course_top_item_active':'course_top_item'"
                  v-for="(item,index) in this.classifyData[1].child"
                  :key="index"
                  @click="gradeClick(index)"
                >{{item.name}}</div>
              </div>
            </div>
            <div class="classify_button">
              <van-button type="default" style="width:50%" @click="resetClick">重置</van-button>
              <van-button type="danger" style="width:50%" @click="sureClick">确定</van-button>
            </div>
          </div>
        </van-dropdown-item>
        <van-dropdown-item title="排序" ref="sort">
          <div class="course_container">
            <div
              :class="['sort_item',{'sort_item_active':sortIndex==index}]"
              @click="sortClick(index)"
              v-for="(item,index) in sortData"
              :key="index"
            >{{item}}</div>
          </div>
        </van-dropdown-item>
        <van-dropdown-item title="筛选" ref="filter">
          <div class="filter_container">
            <div class="filter_wrap" v-for="(item,index) in filterData" :key="index">{{item.id}}</div>
          </div>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>

    <!-- 列表部分 -->
    <div class="course_list_container">
      <van-list
        :immediate-check="isInit"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-cell @click="goCourseDetails(item)" v-for="(item,index) in list" :key="index">
          <div class="list_item">
            <div class="item_top">
              <div style="color:#333333;font-size:14px;">{{item.title}}</div>
              <div>
                <span>{{item.start_play_date|time}}</span>~
                <span>{{item.end_play_date|time}}</span>|
                <span>共{{item.total_periods}}课时</span>
              </div>
            </div>
            <div class="item_avatar" v-for="(val,index) in item.teachers_list" :key="index">
              <div>
                <img style="margin-left:10px;box-sizing:border-box;" :src="val.teacher_avatar" />
                <span
                  style="margin-left:10px;box-sizing:border-box;font-size:10px;color:rgba(0,0,0,0.45);"
                >{{val.teacher_name}}</span>
              </div>
              <div style="width:60px;height:60px;" v-show="item.has_buy==1">
                <img src="../assets/apply.png" style="width:100%;height:100%;" alt />
              </div>
            </div>
            <div class="item_bottom">
              <div style="margin-left:10px;color:#595959;font-size:12px;">{{item.sales_num}}人已报名</div>
              <div
                style="margin-right:10px;"
                :style="item.price==0?'color:green':'orange'"
              >{{item.price|price}}</div>
            </div>
          </div>
        </van-cell>
      </van-list>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  components: {},
  data() {
    return {
      classifyData: [
        {
          id: 1,
          name: "年级",
          parent_id: 0,
          child: [
            {
              id: 1,
              name: "初一"
            },
            {
              id: 2,
              name: "初二"
            },
            {
              id: 3,
              name: "初三"
            },
            {
              id: 4,
              name: "高一"
            },
            {
              id: 5,
              name: "高二"
            }
          ]
        },
        {
          id: 2,
          name: "学科",
          parent_id: 0,
          child: [
            {
              id: 7,
              name: "语文"
            },
            {
              id: 8,
              name: "数学"
            },
            {
              id: 9,
              name: "英语"
            },
            {
              id: 12,
              name: "物理"
            },
            {
              id: 13,
              name: "化学"
            }
          ]
        }
      ],
      filterData: [],
      sortData: ["综合排序", "最新", "最热", "价格从低到高", "价格从高到低"],
      count: 0,
      loading: false,
      list: [],
      finished: false,
      selIndex: -1,
      gradeIndex: -1,
      sortIndex: 0, //排序下标
      isInit: false, //是否初始化加载一次

      courseInfo: {
        page: 1,
        limit: 10,
        course_type: 0,
        classify_id: "",
        order_by: "",
        attr_val_id: 1,
        is_vip: 0
      }
    };
  },
  filters: {
    price(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    },
    time(val) {
      let date = new Date(parseInt(val) * 1000);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h=date.getHours();
      h=h<10?"0"+h:h;
      let minute=date.getMinutes();
      minute=minute<10?"0"+minute:minute;
      return y + "-" + m + "-" + d + "-" + "" + h + ":" + minute;
    }
  },
  computed: {},
  methods: {
    // 去课程详情
    goCourseDetails(item) {
      console.log(item);
      this.$http.get(`/api/app/teacher/${item.id}`).then(resp => {
        console.log(resp.data);
        this.$router.push({
          path: "/courseDetails",
          query: {
            id: item.id,
            data: resp.data.data
          }
        });
      });
      // this.$router.push("/courseDetails");
    },
    // 重置按钮事件
    resetClick() {
      this.selIndex = -1;
      this.gradeIndex = -1;
      this.$refs.ok.toggle();
    },
    // 确定按钮事件
    sureClick() {
      this.$refs.ok.toggle();
      this.$http
        .get(`/api/app/courseBasis?`, {
          params: this.courseInfo
        })
        .then(resp => {
          console.log(resp.data);
          this.list = resp.data.data.list;
        });
      let arr = []; //手机id的数组
      if (this.selIndex != -1) {
        let classId = this.classifyData[0].child[this.selIndex].id;
        arr.push(classId);
      }
      if (this.selIndex != -1) {
        let gradeId = this.classifyData[1].child[this.gradeIndex].id;
        arr.push(gradeId);
      }
      this.courseInfo.attr_val_id = arr.join(",");
      // console.log(this.courseInfo.attr_val_id);
    },
    // 点击分类班级
    classifyClick(index) {
      this.selIndex = index;
    },
    // 点击分类年级
    gradeClick(index) {
      this.gradeIndex = index;
    },
    // 上拉加载
    onLoad() {
      setTimeout(() => {
        this.$http
          .get(`/api/app/courseBasis?page=${++this.courseInfo.page}&limit=10&`)
          .then(resp => {
            // console.log(resp.data.data.list);
            if (this.courseInfo.page != 1) {
              //上拉加载的数据
              this.list = this.list.concat(resp.data.data.list);
              // console.log(this.list);
            }
            this.loading = false;
            // 没有更多
            if (resp.data.data.list.length == 0) {
              this.finished = true;
            }
          });
      }, 1500);
    },
    // 排序事件
    sortClick(index) {
      this.sortIndex = index;
      switch (index) {
        case 0:
          this.courseInfo.order_by = 0;
          this.$http
            .get("/api/app/courseBasis?", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data);
              this.list = res.data.data.list;
            });
          this.$refs.sort.toggle();
          break;
        case 1:
          this.courseInfo.order_by = 1;
          this.$http
            .get("/api/app/courseBasis?", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data);
              this.list = res.data.data.list;
            });
          this.$refs.sort.toggle();
          break;
        case 2:
          this.courseInfo.order_by = 2;
          this.$http
            .get("/api/app/courseBasis?", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data);
              this.list = res.data.data.list;
            });
          this.$refs.sort.toggle();
          break;
        case 3:
          this.courseInfo.order_by = 3;
          this.$http
            .get("/api/app/courseBasis?", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data);
              this.list = res.data.data.list;
            });
          this.$refs.sort.toggle();
          break;
        case 4:
          this.courseInfo.order_by = 4;
          this.$http
            .get("/api/app/courseBasis?", {
              params: this.courseInfo
            })
            .then(res => {
              console.log(res.data);
              this.list = res.data.data.list;
            });
          this.$refs.sort.toggle();
          break;
      }
      // this.$http.get("");
    },
    // 搜索事件
    searchClick() {
      this.$router.push("/searchs");
    }
  },
  mounted() {
    // 默认回到顶部
    document.body.scrollTop = document.documentElement.scrollTop = 0;
    // 请求列表数据    请求已报名字段
    this.$http.get("/api/app/courseBasis?page=1&limit=10&").then(resp => {
      this.list = resp.data.data.list;
      console.log(resp.data);
    });
  }
};
</script>
