<template>
  <div class="details_container">
    <!-- 头部部分 -->
    <div class="details_header">
      <div class="details_header_top">
        <div class="header_wrap">
          <div>&lt;</div>
          <div>讲师详情</div>
        </div>
      </div>
    </div>
    <!-- 姓名部分 -->
    <div class="details_name_container">
      <div class="details_name_wrap">
        <div class="name_left">
          <img :src="teacherData.avatar" alt />
        </div>
        <div class="name_center">
          <div class="name_center_top">
            <div style="color:#555;font-size:18px;">{{teacherData.real_name}}</div>&nbsp;&nbsp;
            <div style="color:orange;fon-size:12px;">{{teacherData.level_name}}</div>
          </div>
          <div class="name_center_bottom">
            <div
              style="color:#B7B7B7;font-size:13px;"
            >{{teacherData.sex|sex}}&nbsp;&nbsp;{{teacherData.teach_age}}年教龄</div>
          </div>
        </div>
        <div class="name_right">
          <div :style="color?'color:orange':'color:#999'" @click="attentionClick">{{this.attention}}</div>
        </div>
      </div>
    </div>
    <!-- vant部分 -->
    <div class="details_vant_container">
      <van-tabs line-width="1.2rem" color="orange" title-active-color="orange" v-model="active">
        <van-tab title="讲师介绍">
          <div class="vant_wrap">
            <div class="vant_top" v-for="(item,index) in vantData.attr" :key="index">
              <div class="vant_left">{{item.attr_name}}</div>
              <div
                class="vant_right"
                v-for="(v,i) in item.attr_value"
                :key="i"
              >{{v.attr_value_name}}</div>
            </div>
          </div>

          <div style="width:100%;display:flex;">
            <div class="vant_left">老师简介</div>
            <div style="width:80%;color:#8C8C8C">{{vantData.intro}}</div>
          </div>
        </van-tab>
        <van-tab title="主讲课程">
          <div class="course_wrap">
            <div class="course_item_container" v-for="(item,index) in courseList.list" :key="index">
              <div class="course_item_top" style="color:#333333;font-size:16px;">{{item.title}}</div>
              <div style="color:#666666;font-size:12px;">共1课时</div>
              <div class="course_name" v-for="(v,i) in item.teachers_list" :key="i">
                <div
                  style="color:#666666;font-size:12px;#;width:28%;display:flex;align-items:center;justify-content:space-around"
                >
                  <img :src="v.teacher_avatar" alt />
                  <div>{{v.teacher_name}}</div>
                </div>
              </div>
              <div class="course_bottom">
                <div style="color:#666666;margin-left:20px;font-size:12px;">{{item.sales_num}}人报名</div>
                <div
                  style="margin-right:20px;"
                  :class="item.price==0?'green':'orange'"
                >{{item.price|price}}</div>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="学员评价">
          <div class="student_container">
            <div class="student_none_container">
              <img src="../assets/empty.png" alt />
              <p>暂无评论</p>
            </div>
            <div class="student_wrap">
              <div
                class="student_item"
                v-for="(item,index) in commentList.tag"
                :key="index"
              >{{item.title}}</div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!-- 底部部分 -->
    <div class="detials_bottom_container">
      <button>立即预约</button>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      flag: 0,
      active: 0,
      vantData: [],
      courseList: [],
      commentList: [],
      attention: "关注",
      color: true,
      teacherData: this.$route.query.data.teacher
        ? this.$route.query.data.teacher
        : {}
    };
  },
  filters: {
    sex(val) {
      if (val == 0) {
        return "男";
      }
    },
    price(val) {
      if (val == 0) {
        return `免费`;
      } else {
        return `${(val / 100).toFixed(2)}`;
      }
    }
  },
  mounted() {
    this.infoData();
    this.courseData();
    this.commentData();
  },
  methods: {
    async infoData() {
      let { data: res } = await this.$http.get(
        `/api/app/teacher/${this.$route.query.id}`
      );
      // console.log(res.data);
      if (res.data.flag == 2) {
        this.attention = "关注";
        this.color = true;
      } else {
        this.attention = "已关注";
        this.color = false;
      }
      this.vantData = res.data;
    },
    async courseData() {
      let { data: res } = await this.$http.post("/api/app/teacher/mainCourse", {
        teacher_id: this.$route.query.id
      });
      // console.log(res.data);
      this.courseList = res.data;
    },
    async commentData() {
      let { data: res } = await this.$http.post("/api/app/teacher/comment", {
        teacher_id: Number(this.$route.query.id),
        page: 1,
        limit: 10
      });
      // console.log(res.data);
      this.commentList = res.data;
    },
    async attentionClick() {
      let { data: res } = await this.$http.get(
        `/api/app/teacher/collect/${this.$route.query.id}`
      );
      // console.log(res.data);
      if (res.data.flag == 2) {
        this.attention = "关注";
        this.color = true;
      } else {
        this.attention = "已关注";
        this.color = false;
      }
    }
  }
};
</script>
